a {
  color: #334CD5; }
  a:hover {
    text-decoration: underline; }
  a:focus {
    outline: LightBlue solid 2px; }

a:link,
a:visited {
  color: #3B55E6; }

.screen-reader-text:hover, .screen-reader-text:active, .screen-reader-text:focus {
  clip-path: none; }

button,
input[type="button"],
input[type="reset"],
input[type="submit"] {
  background: #D30000;
  border-radius: 500px;
  text-transform: none;
  padding-right: 1.4em;
  padding-left: 1.4em;
  font-size: 1rem;
  border: 1px solid #D30000; }
  button.btn-secondary,
  input[type="button"].btn-secondary,
  input[type="reset"].btn-secondary,
  input[type="submit"].btn-secondary {
    background: #FFFFFF;
    color: #D30000; }

input[type="text"],
input[type="email"],
input[type="url"],
input[type="password"],
input[type="search"],
textarea {
  color: #545454; }

.long-text-input {
  width: 100%; }

#buddypress .comment-reply-link,
#buddypress .generic-button a,
#buddypress .standard-form button,
#buddypress a.button,
#buddypress input[type="button"],
#buddypress input[type="reset"],
#buddypress input[type="submit"],
#buddypress ul.button-nav li a,
a.bp-title-button {
  background: #FFFFFF;
  color: #D30000;
  border: 1px solid #D30000;
  border-radius: 500px;
  padding-right: 1.4em;
  padding-left: 1.4em; }

#buddypress .comment-reply-link:hover,
#buddypress .standard-form button:hover,
#buddypress a.button:focus,
#buddypress a.button:hover,
#buddypress div.generic-button a:hover,
#buddypress input[type="button"]:hover,
#buddypress input[type="reset"]:hover,
#buddypress input[type="submit"]:hover,
#buddypress input[type="submit"]:focus,
#buddypress ul.button-nav li a:hover,
#buddypress ul.button-nav li.current a {
  background: #ededed;
  color: black; }

#buddypress .activity-list .activity-content .activity-header,
#buddypress .activity-list .activity-content .comment-header {
  color: #000000; }

#buddypress .acomment-meta a,
#buddypress .activity-header a,
#buddypress .comment-meta a {
  text-decoration: underline; }

#buddypress a.activity-time-since {
  color: #222328; }

#buddypress div#item-header-cover-image .user-nicename a,
#buddypress div#item-header-cover-image .user-nicename {
  text-shadow: 0 0 6px black; }

#buddypress #header-cover-image {
  background-color: #555; }

.site-title a:hover,
.site-title a:active {
  color: #3B55E6; }

.sidebar-navigation-toggle:hover {
  color: #3B55E6; }

.main-navigation-menu a {
  font-weight: 700; }
  .main-navigation-menu a:hover, .main-navigation-menu a:active {
    color: #3B55E6;
    text-decoration: underline; }

.main-navigation-menu ul {
  background: #D30000; }

.main-navigation-menu ul a:hover {
  background: #334CD5; }

.header-social-icons .social-icons-menu li a:hover {
  color: #3B55E6; }

.widget-title a:link,
.widget-title a:visited {
  color: #3B55E6; }

.widget-magazine-posts .widget-header .widget-title {
  color: #D30000; }

.entry-title a:hover,
.entry-title a:active {
  color: #D30000; }

/* Read more Link */
.more-link {
  background: #D30000; }

.entry-categories .meta-category a {
  background: #D30000; }

.entry-categories .meta-category a:hover,
.entry-categories .meta-category a:active {
  color: #E11127; }

.widget_tag_cloud .tagcloud a,
.entry-tags .meta-tags a {
  background: #334CD5; }

.post-navigation .nav-links a:hover,
.post-navigation .nav-links a:active {
  background: #E11127; }

.page-header .archive-title {
  color: #D30000; }

.pagination a:hover,
.pagination .current {
  background: #E11127; }

.infinite-scroll #infinite-handle span:hover {
  background-color: #E11127; }

.comments-header .comments-title,
.comment-reply-title span {
  border: 3px solid #D30000;
  color: #D30000; }

.reply .comment-reply-link:hover,
.reply .comment-reply-link:active {
  background: #E11127; }

@media only screen and (max-width: 60em) {
  .main-navigation-menu .submenu-dropdown-toggle:hover:before,
  .main-navigation-menu .submenu-dropdown-toggle:active:before {
    color: #D30000; } }

.custom-contact .col-sm-6 {
  width: 50%; }

.copyright-divider .list-unstyled {
  list-style: none;
  padding-left: 0; }

.sidebar-navigation {
  padding: 0; }
  .sidebar-navigation .menu-item {
    border-bottom: 1px solid #dddddd; }
  .sidebar-navigation .sub-menu .menu-item {
    border-bottom: 1px solid #E11127; }

@media only screen and (min-width: 60em) {
  .site-header {
    display: none; } }

.front-page-login-form .Grid-cell-liner {
  border: solid 1px #cacaca; }

.front-page-login-form .login-header {
  margin-top: 0; }

.front-page-login-form .login-form-password-label {
  margin-top: .5em; }

.front-page-login-form form[name="login-form"] label {
  display: block; }

.front-page-login-form form[name="login-form"] input[type="submit"] {
  margin: 1em 0; }

.front-page-login-form .log-in-with-sso-forward,
.front-page-login-form .log-in-with-sso {
  display: inline-block; }

.starter-maps-grid .caption {
  margin-top: .2em;
  display: inline-block; }

.vpf-thumbnail,
.map-thumbnail,
.report-thumbnail {
  background-image: url("images/report-thumbnail.png");
  background-repeat: no-repeat;
  max-width: 520px;
  height: 330px;
  margin-bottom: 1em; }

.vpf-thumbnail {
  background-image: url("images/vpf-thumbnail.png"); }

.map-thumbnail {
  background-image: url("images/map-thumbnail.png"); }

/**** AUTOCOMPLETE ***/
.ui-autocomplete {
  border: 1px solid #6D6E71;
  border-radius: 3px;
  font-family: inherit; }

ul.ui-autocomplete {
  background-color: #FFFFFF;
  max-width: 300px;
  list-style-type: none;
  padding-left: 0; }

ul.ui-autocomplete li {
  padding: 8px 4px; }

ul.ui-autocomplete li:hover {
  background-color: #D30000;
  color: white; }

.ui-autocomplete-input {
  min-width: 60px; }

tr.priority_staff_save td {
  padding-bottom: 24px; }

a.submit_staff_partners {
  font-size: 1.5em;
  font-weight: bold; }

.close {
  vertical-align: super; }

/*Plugin overrides*/
.bp-user form#invite-anyone-by-email .description {
  color: #545454; }

#cdt .cdt-submenu .js-toggle-control {
  background-color: #dedede;
  color: #000000; }
  #cdt .cdt-submenu .js-toggle-control.active {
    background-color: #D30000;
    color: #FFFFFF; }

#bbpress-forums #bbp-search-form .screen-reader-text.hidden {
  display: inline-block;
  position: relative !important;
  height: auto;
  width: auto;
  clip: inherit;
  clip-path: inherit;
  left: 0; }

.aha-print-only {
  display: none; }

/* for printer only */
@media only print {
  .aha-print-only {
    display: block; }
  .aha-screen-only,
  #page #top-header,
  .hidden-print {
    display: none !important; }
  .page-template-template-transamerica-report #cdt .cdt-indicator-container:first-of-type {
    margin-top: 1em !important; }
    .page-template-template-transamerica-report #cdt .cdt-indicator-container:first-of-type h3:first-of-type {
      margin-top: 0; } }
